import React, { Fragment, useMemo } from 'react';
import { useWindowSize, useWindowWidth, useWindowHeight } from '@react-hook/window-size';
import { css, Section, Typography } from './branding/system';

import { Paragraph, Button, Icon } from './branding/components';
import Logo from './components/Logo';
import ContactBlock from './components/ContactBlock';

function HorizontalApp({ packageOverview }) {
	return (
		<Section
			themeTypography
			horizontal
			align="center"
			verticalAlign="center"
			className={css`
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
			`}
		>
			<Section horizontal spaceBetween={'large'}>
				<Section fluid padding="medium">
					<Section className={{ width: '100%', maxWidth: '400px' }} vertical spaceBetween={'large'}>
						<Logo />
						<ContactBlock />
					</Section>
				</Section>

				<Section align="center" verticalAlign="center" fluid padding="medium">
					<Section className={{ width: '100%', maxWidth: '400px' }} spaceBetween="medium">
						{packageOverview}
					</Section>
				</Section>
			</Section>
		</Section>
	);
}
function VerticalApp({ packageOverview }) {
	return (
		<Section
			themeTypography
			themeBackground
			vertical
			align="center"
			padding='medium'
		>
			<Section spaceBetween={'medium'}>
				<Logo />

				<Section className={{ width: '100%', maxWidth: '400px' }} spaceBetween="medium">
					{packageOverview}
				</Section>

				<ContactBlock  compact />
			</Section>
		</Section>
	);
}

function App() {
	const width = useWindowWidth(
		360 /* initialWidth when there is no window */,
		720 /* initialHeight when there is no window */,
		{ wait: 100 }
	);
	const isPortraitMode = width < 1100;
	console.log('Width', width);

	const packageOverview = useMemo(
		() => (
			<Fragment>
				<Section>
					<Typography extraBold slightlySmaller>
						FACILITEERT
					</Typography>
					<Paragraph>Alles zo ingericht om jouw inspanningen tot het hoognodige te beperken.</Paragraph>
				</Section>

				<Section>
					<Typography extraBold slightlySmaller>
						ASSISTEERT
					</Typography>
					<Paragraph>24/7 boekhoudkundige assistentie waar gewenst.</Paragraph>
				</Section>

				<Section>
					<Typography extraBold slightlySmaller>
						REGISSEERT
					</Typography>
					<Paragraph>Bedrijfsprocessen ingeregeld en advieswerk op maat voor écht rendement.</Paragraph>
				</Section>
			</Fragment>
		),
		[]
	);

	return isPortraitMode ? (
		<VerticalApp packageOverview={packageOverview} />
	) : (
		<HorizontalApp packageOverview={packageOverview} />
	);
}

export default App;
