import React from 'react';
import { Section, Typography } from '../branding/system';

import { Button, Icon } from '../branding/components';
import * as TEKSTEN from '../TEKSTEN';
export default function ContactBlock({ compact }) {
	return (
		<Section spaceBetween="small">
			<Section spaceBetween="small" horizontal>
				<Button label={compact ? TEKSTEN.EMAIL.kortLabel : TEKSTEN.EMAIL.langLabel} iconBefore="comment" href={'mailto:' + TEKSTEN.EMAIL.adres} dropShadow />
				<Button
					label={compact ? TEKSTEN.EBOOKHOUDING.kortLabel : TEKSTEN.EBOOKHOUDING.langLabel}
					href={TEKSTEN.EBOOKHOUDING.url}
					iconBefore="rocket"
					dropShadow
				/>
			</Section>

			<Section horizontal spaceBetween="small">
				<Section href={'mailto:' + TEKSTEN.EMAIL.adres}>
					<Typography dimmed inline slightlyBold slightlySmaller activeColorName="anthraciet">
						<Icon name="envelope-o" />
						{' ' + TEKSTEN.EMAIL.adres}
					</Typography>
				</Section>
				{TEKSTEN.WHATSAPP.map((number) => (
					<Section key={number} href={'http://wa.me/31' + number.match(/\d+/g).join('')}>
						<Typography dimmed inline slightlyBold slightlySmaller activeColorName="anthraciet">
							<Icon name="whatsapp" />
							{' ' + number}
						</Typography>
					</Section>
				))}
			</Section>
		</Section>
	);
}
